*,
*::before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: sans-serif;
}

a {
  text-decoration: none;
}

.container {
  width: 1800px;
  margin-left: auto;
  margin-right: auto;
}

img {
  width: 100%;
  display: block;
}

.navegacion {
  background-color: #ff8080;
  color: #eee;
  height: 50px;
  width: 100%;
  position: sticky;
  top: 0px;
  display: flex;
  justify-content: space-between;
  z-index: 10;
}

.navegacion-title {
  margin-right: 15px;
  font-size: 2rem;
  order: 2;
  margin-right: 15px;
}

.menu {
  position: fixed;
  top: 50px;
  background-color: #ffb3b3;
  width: 50%;
  height: 100vh;
  transition: transform 0.3s;
  transition-timing-function: cubic-bezier(0.67, -0.05, 0.57, 0.78);
  transform: translateX(-100%);
  z-index: 100;
}
.menu.active {
  transform: translateX(0);
}
.menu.active:after {
  transform: translateX(0);
  background-color: rgba(0, 0, 0, 0.45);
}
.menu:after {
  content: "";
  display: block;
  width: 100%;
  height: 100vh;
  transform: translateX(-500%);
  position: fixed;
  top: 0;
  left: 100%;
  transition: background-color 0.3s linear 0.3s;
}

.menu-item {
  height: 70px;
  width: 100%;
  padding-left: 70px;
  padding-top: 23px;
  position: relative;
  z-index: 100;
}
.menu-item:hover {
  background-color: #ff8a8a;
}

.menu-link {
  font-size: 1.5rem;
  color: inherit;
  padding: 5px 10px;
}

.menu-bar {
  margin-left: 15px;
  padding-top: 6px;
  cursor: pointer;
  order: 1;
  margin-left: 15px;
}
.menu-bar:hover [class^=bar-] {
  background-color: #d5d5d5;
}
.menu-bar:hover [class$=top] {
  transform: translateY(-3.5px) scale(1.1, 1.1);
}
.menu-bar:hover [class$=medium] {
  transform: scale(0.9, 0.9);
}
.menu-bar:hover [class$=bottom] {
  transform: translateY(3.5px) scale(1.1, 1.1);
}
.menu-bar.active [class$=top] {
  transform: scale(1.2, 1.2) translateY(10px) rotate(45deg);
}
.menu-bar.active [class$=medium] {
  position: absolute;
  background-color: transparent;
}
.menu-bar.active [class$=bottom] {
  transform: scale(1.2, 1.2) translateY(2.5px) rotate(-45deg);
}

[class^=bar-] {
  width: 30px;
  height: 5px;
  background-color: white;
  margin-top: 5px;
  transition-timing-function: cubic-bezier(0.67, -0.05, 0.57, 0.78);
  transition: background-color 0.3s, transform 0.3s;
}

@media (min-width: 768px) {
  .navegacion {
    position: relative;
  }
  .navegacion-title {
    margin-right: 0;
    margin-left: 15px;
    order: 1;
  }
  .navegacion .menu-bar {
    position: absolute;
    opacity: 0;
  }
  .navegacion .menu {
    display: flex;
    height: initial;
    width: initial;
    position: relative;
    top: 0;
    transform: translateX(0);
    background-color: transparent;
    margin-right: 15px;
    margin-left: 0;
    order: 2;
  }
  .navegacion .menu:after {
    content: none;
  }
  .navegacion .menu-item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: initial;
    width: initial;
    padding-top: 0;
    padding-left: 10px;
    padding-right: 10px;
  }
  .navegacion .menu-link {
    margin: 0;
  }
}
@media (min-width: 1800px) {
  .navegacion {
    width: 1800px;
    margin-left: auto;
    margin-right: auto;
  }
}